import React, { useState, useEffect } from 'react';
import { View, Text, TextInput, TouchableOpacity, StyleSheet, useWindowDimensions } from 'react-native';

const App = () => {
  const { width, height } = useWindowDimensions();
  const [count, setCount] = useState(0);
  const [time, setTime] = useState(new Date().toLocaleTimeString());
  const [inputValue, setInputValue] = useState('');
  const [displayValue, setDisplayValue] = useState('');

  useEffect(() => {
    const timer = setInterval(() => {
      setTime(new Date().toLocaleTimeString());
    }, 1000);

    return () => clearInterval(timer);
  }, []);

  const handleNumberPress = (num: string) => {
    const newValue = inputValue + num;
    setInputValue(newValue);
    setDisplayValue(newValue ? parseInt(newValue).toLocaleString() : '');
  };

  const handleIncrement = () => {
    const value = parseInt(inputValue) || 1;
    setCount(prev => prev + value);
    setInputValue('');
    setDisplayValue('');
  };

  const handleDecrement = () => {
    const value = parseInt(inputValue) || 1;
    setCount(prev => {
      const newCount = prev - value;
      return newCount < 0 ? 0 : newCount;
    });
    setInputValue('');
    setDisplayValue('');
  };

  return (
    <View style={[styles.container, { padding: width * 0.05 }]}>
      <View style={[styles.header, { marginBottom: height * 0.03 }]}>
        <Text style={[styles.timeText, { fontSize: width * 0.1 }]}>{time}</Text>
        <Text style={[styles.countText, { fontSize: width * 0.12 }]}>{count.toLocaleString()}명</Text>
      </View>
      
      <View style={[styles.inputContainer, { marginBottom: height * 0.02 }]}>
        <TextInput
          style={[styles.input, { height: height * 0.06 }]}
          value={displayValue}
          onChangeText={(text) => {
            const numericValue = text.replace(/,/g, '');
            setInputValue(numericValue);
            setDisplayValue(numericValue ? parseInt(numericValue).toLocaleString() : '');
          }}
          placeholder="증가/감소할 숫자 입력"
          keyboardType="numeric"
          placeholderTextColor="#666"
        />
      </View>

      <View style={[styles.keypad, { marginBottom: height * 0.03 }]}>
        <View style={styles.keypadRow}>
          <TouchableOpacity style={[styles.keypadButton, { width: width * 0.25 }]} onPress={() => handleNumberPress('1')}>
            <Text style={[styles.keypadButtonText, { fontSize: width * 0.06 }]}>1</Text>
          </TouchableOpacity>
          <TouchableOpacity style={[styles.keypadButton, { width: width * 0.25 }]} onPress={() => handleNumberPress('2')}>
            <Text style={[styles.keypadButtonText, { fontSize: width * 0.06 }]}>2</Text>
          </TouchableOpacity>
          <TouchableOpacity style={[styles.keypadButton, { width: width * 0.25 }]} onPress={() => handleNumberPress('3')}>
            <Text style={[styles.keypadButtonText, { fontSize: width * 0.06 }]}>3</Text>
          </TouchableOpacity>
        </View>
        <View style={styles.keypadRow}>
          <TouchableOpacity style={[styles.keypadButton, { width: width * 0.25 }]} onPress={() => handleNumberPress('4')}>
            <Text style={[styles.keypadButtonText, { fontSize: width * 0.06 }]}>4</Text>
          </TouchableOpacity>
          <TouchableOpacity style={[styles.keypadButton, { width: width * 0.25 }]} onPress={() => handleNumberPress('5')}>
            <Text style={[styles.keypadButtonText, { fontSize: width * 0.06 }]}>5</Text>
          </TouchableOpacity>
          <TouchableOpacity style={[styles.keypadButton, { width: width * 0.25 }]} onPress={() => handleNumberPress('6')}>
            <Text style={[styles.keypadButtonText, { fontSize: width * 0.06 }]}>6</Text>
          </TouchableOpacity>
        </View>
        <View style={styles.keypadRow}>
          <TouchableOpacity style={[styles.keypadButton, { width: width * 0.25 }]} onPress={() => handleNumberPress('7')}>
            <Text style={[styles.keypadButtonText, { fontSize: width * 0.06 }]}>7</Text>
          </TouchableOpacity>
          <TouchableOpacity style={[styles.keypadButton, { width: width * 0.25 }]} onPress={() => handleNumberPress('8')}>
            <Text style={[styles.keypadButtonText, { fontSize: width * 0.06 }]}>8</Text>
          </TouchableOpacity>
          <TouchableOpacity style={[styles.keypadButton, { width: width * 0.25 }]} onPress={() => handleNumberPress('9')}>
            <Text style={[styles.keypadButtonText, { fontSize: width * 0.06 }]}>9</Text>
          </TouchableOpacity>
        </View>
        <View style={styles.keypadRow}>
          <TouchableOpacity style={[styles.keypadButton, { width: width * 0.25, opacity: 0 }]}>
            <Text style={[styles.keypadButtonText, { fontSize: width * 0.06 }]}></Text>
          </TouchableOpacity>
          <TouchableOpacity style={[styles.keypadButton, { width: width * 0.25 }]} onPress={() => handleNumberPress('0')}>
            <Text style={[styles.keypadButtonText, { fontSize: width * 0.06 }]}>0</Text>
          </TouchableOpacity>
          <TouchableOpacity style={[styles.keypadButton, { width: width * 0.25, opacity: 0 }]}>
            <Text style={[styles.keypadButtonText, { fontSize: width * 0.06 }]}></Text>
          </TouchableOpacity>
        </View>
      </View>

      <View style={styles.buttonContainer}>
        <TouchableOpacity 
          style={[styles.button, styles.incrementButton, { height: height * 0.07 }]} 
          onPress={handleIncrement}
        >
          <Text style={[styles.buttonText, { fontSize: width * 0.08 }]}>+</Text>
        </TouchableOpacity>
        <TouchableOpacity 
          style={[styles.button, styles.decrementButton, { height: height * 0.07 }]} 
          onPress={handleDecrement}
        >
          <Text style={[styles.buttonText, { fontSize: width * 0.08 }]}>-</Text>
        </TouchableOpacity>
      </View>
    </View>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: '#121212',
  },
  header: {
    alignItems: 'center',
    backgroundColor: '#1E1E1E',
    padding: 20,
    borderRadius: 15,
    width: '100%',
    shadowColor: '#000',
    shadowOffset: {
      width: 0,
      height: 2,
    },
    shadowOpacity: 0.25,
    shadowRadius: 3.84,
    elevation: 5,
  },
  timeText: {
    marginBottom: 10,
    color: '#FFD700',
    fontWeight: 'bold',
  },
  countText: {
    color: '#FFFFFF',
    fontWeight: 'bold',
    textShadowColor: 'rgba(0, 0, 0, 0.3)',
    textShadowOffset: { width: 0, height: 2 },
    textShadowRadius: 4,
  },
  inputContainer: {
    width: '100%',
  },
  input: {
    width: '100%',
    backgroundColor: '#1E1E1E',
    borderRadius: 10,
    paddingHorizontal: 20,
    color: '#FFFFFF',
    borderWidth: 1,
    borderColor: '#333333',
    shadowColor: '#000',
    shadowOffset: {
      width: 0,
      height: 2,
    },
    shadowOpacity: 0.25,
    shadowRadius: 3.84,
    elevation: 5,
    textAlign: 'center',
  },
  keypad: {
    width: '100%',
    backgroundColor: '#1E1E1E',
    padding: 10,
    borderRadius: 15,
    shadowColor: '#000',
    shadowOffset: {
      width: 0,
      height: 2,
    },
    shadowOpacity: 0.25,
    shadowRadius: 3.84,
    elevation: 5,
  },
  keypadRow: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    marginBottom: 10,
  },
  keypadButton: {
    aspectRatio: 1,
    justifyContent: 'center',
    alignItems: 'center',
    backgroundColor: '#2D2D2D',
    marginHorizontal: 5,
    borderRadius: 10,
    borderWidth: 1,
    borderColor: '#333333',
  },
  keypadButtonText: {
    color: '#FFFFFF',
  },
  buttonContainer: {
    flexDirection: 'row',
    justifyContent: 'space-around',
    width: '100%',
  },
  button: {
    width: '45%',
    borderRadius: 10,
    justifyContent: 'center',
    alignItems: 'center',
    shadowColor: '#000',
    shadowOffset: {
      width: 0,
      height: 2,
    },
    shadowOpacity: 0.25,
    shadowRadius: 3.84,
    elevation: 5,
  },
  incrementButton: {
    backgroundColor: '#2E7D32',
  },
  decrementButton: {
    backgroundColor: '#C62828',
  },
  buttonText: {
    color: '#FFFFFF',
    fontWeight: 'bold',
    textShadowColor: 'rgba(0, 0, 0, 0.3)',
    textShadowOffset: { width: 0, height: 2 },
    textShadowRadius: 4,
  },
});

export default App;
